import PropTypes from 'prop-types';
import * as Yup from 'yup';
import { useState, useEffect, useMemo } from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
// form
import { useForm} from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Stack, Typography  } from '@mui/material';

// routes
import { PATH_ADMIN } from '../../routes/paths';
import { FormProvider, RHFSelect, RHFSwitch, RHFTextField } from '../../components/hook-form';

import axios from '../../utils/axios';
// ----------------------------------------------------------------------

CreateRechargeCardPlan.propTypes = {
  isEdit: PropTypes.bool,
  currentUser: PropTypes.object,
};
export default function CreateRechargeCardPlan({ isEdit, currentUser }) {
  const { enqueueSnackbar } = useSnackbar();
  const [adexnetwork, setNetwork] = useState();


  const NewUserSchema = Yup.object().shape({
    name: Yup.string().required('Plan Name Required'),
    network: Yup.string().required('Network Required'),
    load_pin: Yup.string().required('Load Pin Required'),
    smart: Yup.string().required('Smart Price Required'),
    awuf: Yup.string().required('Awuf Price Required'),
    agent: Yup.string().required('Agent Price Required'),
    api: Yup.string().required('Api Price Required'),
    special: Yup.string().required('Special Price Required'),
    check_balance: Yup.string().required()
  });
  const defaultValues = useMemo(
    () => ({
     network: currentUser?.network || '',
      name: currentUser?.name || '',
      smart: currentUser?.smart || '',
      awuf: currentUser?.awuf || '',
      agent: currentUser?.agent || '',
      special: currentUser?.special || '',
      api: currentUser?.api || '',
      load_pin: currentUser?.load_pin || '',
      plan_status: currentUser?.plan_status || false,
      plan_id: currentUser?.plan_id || '',
      adex1: currentUser?.adex1 || '',
      adex2: currentUser?.adex2 || '',
      adex3: currentUser?.adex3 || '',
      adex4: currentUser?.adex4 || '',
      adex5: currentUser?.adex5 || '',
      free1: currentUser?.free1 || '',
      free2: currentUser?.free2 || '',
      free3: currentUser?.free3 || '',
      check_balance: currentUser?.check_balance || '',
      
    }),
    [currentUser]
  );
  const AccessToken =  window.localStorage.getItem('accessToken');
  const navigate = useNavigate();
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
    // defaultValues,
  });

  const {
    reset,
    watch,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;
  useEffect(() => {
    if (isEdit && currentUser !== undefined) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    axios.get('/api/website/app/network').then(data => {
        setNetwork(data.data?.network);
     });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentUser]);
 const  values = watch();
  const onSubmit = async (data) => {
     try{
    await axios.post(`/api/new_recharge_card_plan/${AccessToken}/secure`,data);
     enqueueSnackbar('Create success!');
     navigate(PATH_ADMIN.plan.recharge_card_plan);
     }catch(error){
        if(error.data?.message !== undefined){
            enqueueSnackbar(error.data?.message, { variant: 'error' })  
             } else if(error?.message !== undefined){
                enqueueSnackbar(error?.message, { variant: 'error' })   
             }else{
                enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
             }
     }
  }
  const onCreateReset = async (data) => {
try{
    await axios.post(`/api/new_recharge_card_plan/${AccessToken}/secure`,data);
reset();
enqueueSnackbar('Create success!');
}catch(error){
if(error.data?.message !== undefined){
    enqueueSnackbar(error.data?.message, { variant: 'error' })  
     } else if(error?.message !== undefined){
        enqueueSnackbar(error?.message, { variant: 'error' })   
     }else{
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
     }
}
  }

  const onEdit = async(data)=>{
try{
await axios.post(`/api/edit_new_recharge_card_plan/${AccessToken}/secure`,data);
navigate(PATH_ADMIN.plan.recharge_card_plan);
enqueueSnackbar('Update success!');
}catch(error){
if(error.data?.message !== undefined){
    enqueueSnackbar(error.data?.message, { variant: 'error' })  
     } else if(error?.message !== undefined){
        enqueueSnackbar(error?.message, { variant: 'error' })   
     }else{
        enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
     }
}
  }

  const onEditReset = async(data) => {
    try{
        await axios.post(`/api/edit_new_recharge_card_plan/${AccessToken}/secure`,data);
navigate(PATH_ADMIN.plan.new_recharge_card_plan);
enqueueSnackbar('Update success!');
        }catch(error){
        if(error.data?.message !== undefined){
            enqueueSnackbar(error.data?.message, { variant: 'error' })  
             } else if(error?.message !== undefined){
                enqueueSnackbar(error?.message, { variant: 'error' })   
             }else{
                enqueueSnackbar('Something Went Wrong', { variant: 'error' })   
             }
        } 
  }


  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(!isEdit? onSubmit : onEdit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: 'grid',
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
                <RHFSelect name="network" label="Network" placeholder="Network" defaultValue="MTN">
                <option value="" />
                {adexnetwork?.map((option) => (
                  <option key={option?.plan_id} value={option?.network}>
                    {option?.network}
                  </option>
                ))}
              </RHFSelect>
              <RHFTextField name="name" label="Plan Name"  defaultValue="0" type="number" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
              <RHFTextField name="load_pin" label="Load Pin"  defaultValue="Load Pin" />
              <RHFTextField name="check_balance" label="Check Balance"  defaultValue="Check Balance" />
              <RHFTextField name="smart" label="Smart Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
              <RHFTextField name="awuf" label="Awuf Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
              <RHFTextField name="agent" label="Agent Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
              <RHFTextField name="api" label="Api Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
              <RHFTextField name="special" label="Special Price" type="number" defaultValue="0" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}/>
              <RHFSwitch
              name="plan_status"
              labelPlacement="start"
              label={
                <>
                  <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                   {values.plan_status? 'ACTIVE' : 'DEACTIVATED'}
                  </Typography>
                </>
              }
             
              sx={{ mx: 0, width: 1, justifyContent: 'space-between' }}
            />
             
              <RHFTextField name="adex1" label="ADEX 1 PLAN ID"  defaultValue = "adex1"/>
              <RHFTextField name="adex2" label="ADEX 2 PLAN ID"  defaultValue = "adex2"/>
              <RHFTextField name="adex3" label="ADEX 3 PLAN ID"  defaultValue = "adex3"/>
              <RHFTextField name="adex4" label="ADEX 4 PLAN ID"  defaultValue = "adex4"/>
              <RHFTextField name="adex5" label="ADEX 5 PLAN ID"  defaultValue = "adex5"/>
              <RHFTextField name="free1" label="FREE 1 PLAN ID"  defaultValue = "free1"/>
              <RHFTextField name="free2" label="FREE 2 PLAN ID"  defaultValue = "free2"/>
              <RHFTextField name="free3" label="FREE 3 PLAN ID"  defaultValue="free3"/>
                   
            
            </Box>

            <Stack alignItems="flex-end" sx={{ mt: 2, display: 'grid',
                columnGap: 1,
                rowGap: 2,  gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },}}>

              <LoadingButton type="submit" variant="contained" loading={isSubmitting}>
                {!isEdit ? 'Create  then Return' : 'Save Changes then Return'}
              </LoadingButton>
               <LoadingButton type="button" onClick={handleSubmit(!isEdit? onCreateReset : onEditReset)} variant="contained" color="warning" loading={isSubmitting}>
               {!isEdit ? ' Create then Reset' : 'Save Changes then Create New'}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}